import React from 'react'
import css from './footer.css'

class Footer extends React.Component {
    render () {
      return <div className={css.footer}>
        
        <div className={css.contacts}>
          <div>
          <img src="data:image/png;base64,"/>
          Tremonton | UT
          </div>
          <div>
          <img className={css.phone}src="data:image/png;base64,"/>
          (123)-456-7890
          </div>
          <div>
          <img className={css.mail}src="data:image/png;base64,"/>
          email@gmail.com
          </div>
        </div>
        <div>
          links
        </div>
        <div>
          rights reserved
        </div>
      </div>
    }
  }

  export default Footer